.outertabDiv-Conditions {
    position: relative;
    color: #000;
    height: 80%;
    top: 40% !important;
    left: 50%;
    transform: translate(-50%, -50%);
    overflow: hidden;
    background-position: -61px -37px;
    background-size: 112% 112%;
    background-repeat: no-repeat;
    background-image: url('../../Assets/img/propertypanel@2x.png');
}
.outertabDiv-subConditions {
    height: 100%;
    width: 96%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 25% 18% 31% 22%;
    border-radius: 11%;
}

.outertabDiv-Condition-nobackground {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #000;
    height: 100%;
    width: 85%;
    overflow: hidden;
}

.HexDecisionPanel_Container {
    height: 109%;
    width: 84%;
    position: absolute;
    top: 53%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 19999;
    overflow: hidden;
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 25% 18% 32% 25%;
    border-radius: 11%;
}

.hexsub_Container1 {
    display: grid;
    grid-template-columns: 30% 50% 20%;
    grid-template-rows: 100%;
    grid-template-areas:
        'hexcontent1_text1 hexcontent1_text2 hexcontent1_text3'
        ' hexcontent1_text1 hexcontent1_text2 hexcontent1_text3'
        'hexcontent1_text1 hexcontent1_text2 hexcontent1_text3 ';
}
#hexcontent1_text1 {
    grid-area: hexcontent1_text1;
    position: relative;
}
#hexcontent1_text2 {
    grid-area: hexcontent1_text2;
    position: relative;
}
#hexcontent1_text3 {
    grid-area: hexcontent1_text3;
    position: relative;
}

