body {
    background-color: #fafafa;
}
.Main_Remote {
    height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-rows: 12% 78% 10%;
    grid-template-columns: 100%;
}
.Remote_Header {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: 7% 13% 66% 7% 7%;
    grid-template-rows: 100%;
}
.Back_BTN {
    height: auto;
    width: 4%;
    position: absolute;
    top: 4%;
    left: 2%;
    cursor: pointer;
}
.Play_Remote {
    height: auto;
    width: 10%;
    font-size: 25px;
    position: absolute;
    top: 5.5%;
    font-family: 'Halcyon_SemiBold' !important;
}
.Help_Btn {
    height: auto;
    width: 4%;
    position: absolute;
    cursor: pointer;
    top: 4%;
    right: 6%;
}
.Bluetooth_Button {
    height: auto;
    width: 6%;
    position: absolute;
    cursor: pointer;
    top: 4%;
    right: 0.1%;
}
.Remote_Body {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: 35% 30% 35%;
    grid-template-rows: 100%;
}
.Buzzer_Lefteye {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 50% 50%;
    grid-template-columns: 100%;
    /* position: absolute; */
    /* top: 10%; */
}
.Ace_Teeth {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-rows: 70% 30%;
    grid-template-columns: 100%;
    /* border: 1px solid red; */
}

.Func_Righteye {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 50% 50%;
    grid-template-columns: 100%;
}
.Left_Eye_Backdround {
    height: auto;
    width: 28%;
    position: absolute;
    top: 66%;
    left: 18%;
    transform: translate(-50%, -50%);
}
