.Left_Partition {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 80% 20%;
    grid-template-columns: 100%;
}
.Right_Partition {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 80% 20%;
    grid-template-columns: 100%;
}
.Buzzer_Partition {
    height: 100%;
    width: 100%;
    display: grid;
    /* position: relative; */
    grid-template-rows: 80% 20%;
    grid-template-columns: 100%;
}

.Slider_Div1 {
    height: 20%;
