.outertabDiv-modal {
    position: relative;
    top: 49vh;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 65vw;
    height: 100vh;
    background-image: url('../../../../../../Assets/img/propertypanel@2x.png');
    background-size: contain;
    background-repeat: no-repeat;
}
.outertabDiv-modals {
    height: 75vh;
    width: 118%;
    background-image: url('../../../../../../Assets/img/propertypanel@2x.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    position: absolute;
    top: 43vh;
    left: 50%;
    transform: translate(-50%, -50%);
}

.FlowchartDecisionPanel_Container {
    height: 85%;
    width: 84%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 19999;
    overflow: hidden;
    /* border: 3px solid blue; */
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 25% 18% 32% 25%;
    border-radius: 11%;
}
/* first div */
.Flowsub_Container1 {
    display: grid;
    grid-template-columns: 30% 50% 20%;
    grid-template-rows: 100%;
    grid-template-areas:
        'content1_text1 content1_text2 content1_text3'
        ' content1_text1 content1_text2 content1_text3'
        'content1_text1 content1_text2 content1_text3 ';
}
#Flowcontent1_text1 {
    grid-area: content1_text1;
    position: relative;
}
#Flowcontent1_text2 {
    grid-area: content1_text2;
    position: relative;
}
#Flowcontent1_text3 {
    grid-area: content1_text3;
    position: relative;
}

