body {
    overflow: hidden;
}

.user-select {
    -webkit-user-select: none; /* Safari */
    -ms-user-select: none; /* IE 10 and IE 11 */
    user-select: none; /* Standard syntax */
}

.assmeblySidebar {
    height: 260px;
    overflow: hidden auto;
    background-color: rgb(53, 53, 53);
    border: 2px solid rgb(64, 61, 61);
    overflow-wrap: break-word;
    margin-top: 2%;
    margin-bottom: 2%;
    margin-left: 3%;
}
