.toast {
    min-width: 150px;
    /* margin-left: -125px; */
    background-color: #d2d2d2;
    color: #000;
    text-align: center;
    border-radius: 25px;
    font-family: 'Halcyon_Regular' !important;
    padding: 16px;
    position: absolute;
    z-index: 5;
    top: 90%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 17px;
}
.toast-active {
    display: block;
}
.toast-inactive {
    display: none;
}

