.wdt {
    position: relative;
    text-align: center;
    /* color: black; */
    outline: none;
    left: 2%;
}
.Hel {
    position: absolute;
    top: 26%;
    left: 42.3%;
    transform: translate(-50%, -50%);
    width: 8%;
}
.Hel_P {
    background-color: white;
    padding: 1px;
    /* word-wrap: break-word; */
    font-size: 1.8vh;
    text-align: left;
    color: #707070;
    font-family: 'Halcyon_Regular' !important;
    border-radius: 12px;
    margin-bottom: 0px !important;
}
.Rem {
    position: absolute;
    top: 75%;
    right: 50.5%;
    transform: translate(-50%, -50%);
    line-height: 1.4;
    background: #ffffff;
    padding: 14px, 28px;
    border-radius: 12px;
    width: 7%;
}
.Rem_P {
    background-color: white;
    padding: 1px;
    font-size: 1.8vh;
    text-align: left;
    color: #707070;
    font-family: 'Halcyon_Regular' !important;
    border-radius: 12px;
    margin-bottom: 0px;
}
.con {
    position: absolute;
    top: 55%;
    left: 61.5%;
    transform: translate(-50%, -50%);
    line-height: 1.4;
    background: #ffffff;
    border-radius: 12px;
    width: 4%;
}
.con_P {
    background-color: white;
    padding: 6px;
    /* word-wrap: break-word; */
    font-size: 1.8vh;
    text-align: left;
    color: #707070;
    font-family: 'Halcyon_Regular' !important;
    border-radius: 12px;
    margin-bottom: 0px;
}
.img {
    width: 100%;
}
.slick-dots li button {
    font-size: 16px;
    line-height: 0;
    display: block;
    width: 20px;
    height: 20px;
    padding: 5px;
    cursor: pointer;
    color: transparent;
    border-radius: 10;
    outline: none;
    background: transparent;
}
.slick-dots li button:before {
    font-family: 'slick';
    font-size: 16px;
    line-height: 20px;
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: 20px;
    content: '•';
    text-align: center;
    opacity: 0.25;
    /* color: black; */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.slick-dots {
    position: absolute;
    bottom: 80px;
    left: -3%;
    display: block;
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
    text-align: center;
}
.slick-slider {
    position: relative;
    display: block;
    overflow: hidden;
    margin-top: -1%;
    /* margin-left: 7%; */
    box-sizing: border-box;
    -webkit-user-select: none;
    user-select: none;
    -webkit-touch-callout: none;
    -khtml-user-select: none;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}
.slick-slide img {
    display: grayscale;
}
@media only screen and (max-device-width: 1080px) {
    .Hel {
        top: 30%;
        left: 41.5%;
    }
    .Rem {
        right: 51%;
    }
}
