/* large screen */

@media only screen and (max-width: 1600px) and (min-width: 700px) {
    .visualProgram-container .VP-hr {
        width: 80px;
        height: 4px;
        position: absolute;
        /* border: 1px solid red; */
        background: #30a8ce;
        outline: none;
        border: none;
        /* changes start */
        top: 18.5%;
        left: -65%;
        transform: translate(-50%, -50%);
    }
}

#popup-text{
    visibility: hidden;
    min-width: 150px;
    margin-left: -125px;
    background-color: #d2d2d2;
    color: #000;
    text-align: center;
    border-radius: 25px;
    font-family: 'Halcyon_Regular' !important;
    padding: 16px;
    position: fixed;
    z-index: 1;
    left: 40%;
    bottom: 5%;
    font-size: 17px;
}
#popup-text.show{
    visibility: visible;
    -webkit-animation: fadein 0.2s, fadeout 0.5s 4s;
    animation: fadein 0.2s, fadeout 0.5s 4s;
}


@-webkit-keyframes fadein {
    from {
        bottom: 5%;
        opacity: 10%;
    }
    to {
        bottom:  5%;
        opacity: 80%;
    }
}

@keyframes fadein {
    from {
        bottom:  5%;
        opacity: 10%;
    }
    to {
        bottom:  5%;
        opacity: 80%;
    }
}

@-webkit-keyframes fadeout {
    from {
        bottom:  5%;
        opacity: 10%;
    }
    to {
        bottom:  5%;
        opacity: 80%;
    }
}

@keyframes fadeout {
    from {
        bottom:  5%;
        opacity: 10%;
    }
    to {
        bottom:  5%;
        opacity: 80%;
    }
}