.select-container {
    height: 100vh;
    width: 100vw;
}

.select-navbar {
    height: 10%;
    width: 100%;
    background-color: #f0f7fa;
    position: relative;
}

.select-item {
    height: 100%;
    width: 250px;
}

.btnGroup-hexapod {
    position: absolute;
    padding: 2rem;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    grid-gap: 1.5rem;
    top: 50%;
    left: 50.5%;
    transform: translate(-50%, -50%);
    width: 23rem;
    height: 26rem;
}

.btnGroup-humanoid {
    position: absolute;
    padding: 2rem;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    grid-gap: 1.5rem;
    top: 50%;
    left: 50.5%;
    transform: translate(-50%, -50%);
    width: 23rem;
    height: 26rem;
}

.btnGroup-klaw {
    position: absolute;
    padding: 2rem;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    grid-gap: 1.5rem;
    top: 50%;
    left: 50.5%;
    transform: translate(-50%, -50%);
    width: 23rem;
    height: 26rem;
    align-content: center;
    justify-content: center;
}

.btnGroup_H {
    position: absolute;
    padding: 2rem;
    top: 50%;
    left: 70%;
    transform: translate(-50%, -50%);
}

.offBtn,
.onBtn {
    width: 3.5rem;
    height: 3.5rem;
    font-size: 1.2rem;
    padding-top: 0.5rem;
    border: None;
    border-radius: 2rem;
    font-family: halcyon_SemiBold;
}

.offBtn {
    color: black;
    background: linear-gradient(to right, #f0eded, white);
}

.onBtn {
    color: white;
    background: linear-gradient(to right, #fda84e, #f97d62);
}

.navbar-item-select {
    color: #fff;
    font-size: 18px;
    font-family: halcyon_SemiBold !important;
    position: absolute;
    top: 50%;
    left: 8%;
    transform: translate(-8%, -50%);
}
.navbar-item-center {
    color: black;
    font-size: 18px;
    font-family: halcyon-SemiBold;
    position: absolute;
    top: 50%;
    left: 22%;
    transform: translate(-25%, -50%);
}

.select-helpbutton {
    height: 73%;
    width: 50px;
    position: absolute;
    top: 45%;
    right: 6%;
    transform: translate(-6%, -45%);
