@media (max-width: 768px) {
    .select-item {
        width: 125px;
    }

    .select-helpbutton {
        height: 73%;
        width: 40px;
        margin-right: 40px;
    }

    .select-bluetooth {
        height: 50px;
        width: 80px;
        margin-top: 5px;
    }
    .select-savebutton {
        height: 73%;
        width: 40px;
        margin-right: 60px;
    }
}

@media (min-width: 769px) and (max-width: 1224px) {
    .select-item {
        width: 125px;
    }

    .select-helpbutton {
        height: 73%;
        width: 40px;
        margin-right: 30px;
    }

    .select-bluetooth {
        height: 50px;
        width: 80px;
        margin-top: 5px;
    }
    .select-savebutton {
        height: 73%;
        width: 40px;
        margin-right: 30px;
    }
}

@media (max-width: 900px) {
    .sm-popcard-button {
        height: 35px;
        width: 35px;
    }

    .sm-toggle-button {
        height: 22px;
        width: 22px;
    }
    .select-backbutton,
    .select-nextbutton {
        height: 62px;
        width: 62px;
    }
}
