.InternalAccessoriesScreen-Item2_v1 .onput_v1 .outputContiner_v1 {
    width: 70%;
    height: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: grid;
    grid-template-rows: repeat(3, 33%);
    grid-template-columns: 100%;
}

