.Connect_Device {
    width: 100vw;
    height: 100vh;
    display: grid;
    background-color: #fafafa;
    /* border: 1px solid red; */
    grid-template-rows: 8% 58% 18% 12%;
    grid-template-columns: 100%;
}

.Connect_Header {
    height: 100%;
    width: 98%;
    display: grid;
    margin: 1.5% 0% 0% 1.5%;
    /* border: 1px solid red; */
    grid-template-columns: 5% 20% 61% 7% 7%;
    grid-template-rows: 100%;
}
.back_btn {
    text-align: center;
    height: auto;
    /* changes start*/
    width: 4%;
    /* changes start */
    position: absolute;
    cursor: pointer;
    top: 4%;
    left: 2%;
    /* margin-top: 10%; */
    /* margin-left: 10%; */
}

.PlayComp_txt {
    font-size: 25px;
    height: auto;
    width: 18%;
    position: absolute;
    /* changes start*/
    top: 5.5%;
    left: 8%;
    /* changes end */
    /* border: 1px solid red; */
    /* font-weight: bold; */
    font-family: 'Halcyon_SemiBold';
    /* margin-top: 7.5%; */
    /* margin-left: 3%; */
}
.helpBtn {
    height: auto;
    position: absolute;
    cursor: pointer;
    top: 4%;
    width: 4%;
    right: 4%;
}
.Devices_Button {
    height: auto;
    width: 4%;
    position: absolute;
    top: 4%;
}

.Connect_div {
    height: 60%;
    width: 45%;
    position: absolute;
    top: 69%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.Computer_Img {
    width: 65%;
    height: auto;
    position: absolute;
    top: 55%;
    left: 51%;
    transform: translate(-50%, -50%);
}
.Pc_Image {
    width: 13%;
    height: auto;
    position: absolute;
    top: 53.5%;
    left: 21%;
    transform: translate(-50%, -50%);
    z-index: 10;
}
.Connect_Footer {
    height: 100%;
    width: 98%;
    display: grid;
    margin: 1.5% 0% 0% 1.5%;
    grid-template-columns: 80% 20%;
    grid-template-rows: 100%;
}
