.Main_Select {
    height: 100vh;
    width: 100vw;
    /* position: relative; */
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 10% 20% 50% 20%;
}

/* .Select_Headers {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 100%;
    grid-template-columns: 7% 25% 30% 30% 10%;
    grid-template-areas: 'Sbackbtn  SPlay_Comp SEmpty_div SAdvance_Setting SHelp_Bttn';
} */
#Sbackbtn {
    grid-area: Sbackbtn;
    position: relative;
}
.Back_Btns {
    width: 6.7vh;
    position: absolute;
    top: 60%;
    left: 65%;
    transform: translate(-50%, -50%);
}

#SPlay_Comp {
    grid-area: SPlay_Comp;
    position: relative;
}
.Play_Comp_text {
    font-size: 2.5vh;
    position: absolute;
    top: 55%;
    left: 0%;
    transform: translate(0%, -50%);
    font-family: 'Halcyon_SemiBold';
}

#SEmpty_div {
    grid-area: SEmpty_div;
}
#SAdvance_Setting {
    grid-area: SAdvance_Setting;
    position: relative;
}
.Advance_Setting_Img {
    width: 7vh;
    position: absolute;
    top: 58%;
    right: 0%;
    transform: translate(0%, -50%);
}

#SHelp_Bttn {
    grid-area: SHelp_Bttn;
    position: relative;
}
.Help_Bttn_Img {
    width: 7vh;
    position: absolute;
    top: 58%;
    right: 33%;
    transform: translate(-50%, -50%);
}
.Play_Divv {
    height: 30%;
    width: 100%;
    margin-top: 75%;
    /* position: absolute; */
}
.Play_Body {
    display: grid;
    grid-template-columns: (20% 20% 20% 20% 20%);
    grid-template-rows: 100%;
    grid-template-areas: ' play learn data code app';
    /* overflow-x: auto; */
    overflow-x: scroll;
    overflow-y: hidden;
}

.Play_Body::-webkit-scrollbar {
    display: none;
}
.Play_Body0::-webkit-scrollbar {
    display: none;
}
.Play_Body0 {
    display: grid;
    grid-template-columns: (25% 25% 25% 25%);
    grid-template-rows: 100%;
    grid-template-areas: ' play learn code app';
    overflow-x: auto;
}

#play {
    grid-area: play;
    position: relative;
}

#learn {
    grid-area: learn;
    position: relative;
}

#data {
    grid-area: data;
    position: relative;
}
#code {
    grid-area: code;
    position: relative;
}
#app {
    grid-area: app;
    position: relative;
}

.sub_body {
    height: 66%;
    width: 62%;
    /* border: 1px solid red; */
    position: absolute;
    top: 50%;
    left: 48%;
    transform: translate(-50%, -50%);
    cursor: pointer;
}

.text {
    font-size: 3vh;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateY(25vh);
    color: white;
}

.Play_Button {
    height: 400px;
    width: 380px;
    position: absolute;
    top: 55%;
    z-index: -1;
    transform: translate(-50%, -50%);
}
.Play_Btn {
    height: auto;
    width: 17.5%;
    position: absolute;
    top: 63%;
    left: 24.5%;
    transform: translate(-50%, -50%);
}
.Program_Button {
    height: 400px;
    width: 380px;
    position: absolute;
    top: 55%;
    z-index: -1;
    transform: translate(-50%, -50%);
}
.Learn_Button {
    height: 400px;
    width: 380px;
    position: absolute;
    top: 56%;
    z-index: -1;
    transform: translate(-50%, -50%);
    border: '2px solid red';
}
.Build_Button {
    height: 400px;
    width: 380px;
    position: absolute;
    top: 54%;
    z-index: -1;

    transform: translate(-50%, -50%);
}
.Play_txt {
    width: 45px;
    /* height: 20px; */
    font-size: 27px;
    font-family: 'Halcyon_SemiBold' !important;
    color: #fff;
    text-align: center;
    position: absolute;
    cursor: pointer;
    top: 59.5%;
    margin-left: -3%;
    transform: translate(-50%, -50%);
}
.Program_txt {
    width: 85px;
    /* height: 25px; */
    font-size: 27px;
    color: #fff;
    font-family: 'Halcyon_SemiBold' !important;
