    cursor: pointer;

    top: 59.5%;
    margin-left: -3%;
    transform: translate(-50%, -50%);
}
.Learn_txt {
    width: 85px;
    /* height: 25px; */
    font-size: 27px;
    color: #fff;
    font-family: 'Halcyon_SemiBold' !important;
    text-align: center;
    position: absolute;
    cursor: pointer;

    top: 60%;
    margin-left: -3%;
    transform: translate(-50%, -50%);
}
.Build_txt {
    width: 45px;
    /* height: 25px; */
    font-size: 27px;
    font-family: 'Halcyon_SemiBold' !important;
    color: #fff;
    text-align: center;
    cursor: pointer;
    display: inline-block;
    position: absolute;
    top: 59.5%;
    margin-left: -4%;
