}

.S_slide {
    width: 100vw;
    height: 100vh;
    position: absolute;
    left: -920%;
    z-index: 100000;
    background: #fff;
}
.helpClose {
    width: 4.5vh;
    cursor: pointer;
}
.load_gif {
    position: absolute;
    top: 45vh;
    left: 45vw;
}

