.SavenavbarContainer {
    width: 100%;
    height: 10vh;
    /* background-color: red; */
}

.savePageConatiner .item-2 {
    width: 85%;
    height: 80%;
    /* border: 1px solid red; */
    margin-top: 9vh;
    margin-left: 10%;
    display: grid;
    grid-template-columns: 45% 45%;
    grid-template-rows: 100%;
    grid-column-gap: 10%;
}

.SavePageinputdetails {
    height: 90%;
    width: 100%;
    /* border: 1px solid red; */
}
.SavePageinputdetails input[type='text'] {
    outline: none;
    border: none;
    font-size: 16px;
}

.SavePageImgdetails {
    width: 100%;
    height: 90%;
    position: relative;
}

.SavePageinputdetails .nameInputDetails {
    width: 100%;
    background: #f5f5f5;
    padding: 15px 20px;
    margin-bottom: 20px;
}

.SavePageinputdetails .descriptionInputDetails {
    width: 100%;
    background: #f5f5f5;
    height: 50%;
    margin-bottom: 20px;
    outline: none;
    border: none;
    font-size: 16px;
    padding-top: 10px;
    padding-left: 20px;
    resize: none;
}
.navbarContainer {
