#SaveAlert1 {
    visibility: hidden;
    min-width: 160px;
    margin-left: -75px;
    background-color: #f5f5f5e6;
    color: #000;
    text-align: center;
    border-radius: 25px;
    font-family: 'Halcyon_Regular' !important;
    padding: 12px;
    position: fixed;
    z-index: 99999;
    font-weight: bold;
    left: 47%;
    bottom: 2.7%;
    font-size: 17px;
}
#SaveAlert1.show {
    visibility: visible;
    -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
    animation: fadein 0.5s, fadeout 0.5s 2.5s;
}
@media only screen and (max-device-width: 768px) {
    .navbarContainer2 {
