.remote-container {
    height: 100vh;
    width: 100vw;
    position: relative;
}

.remote-backbutton {
    height: 52px;
    width: 52px;
    position: absolute;
    top: 30px;
    left: 31px;
    cursor: pointer;
}

.remote-backbutton-title {
    font-size: 16px;
    position: absolute;
    top: 40px;
    left: 101px;
    font-family: halcyon-SemiBold !important;
}

.remote-helpbutton {
    height: 52px;
    width: 52px;
    position: absolute;
    top: 30px;
    right: 81px;
    cursor: pointer;
}

.remote-bluetooth-button {
    height: 52px;
    width: 80px;
    position: absolute;
    top: 30px;
    right: 0px;
    cursor: pointer;
}
.remote-zing-image {
    height: 219px;
    width: 173px;
    position: absolute;
    top: 114px;
    left: 320px;
}

.sub-container {
    width: 95%;
    position: absolute;
    top: 29%;

    /* border: 1px solid green; */
    display: grid;
    grid-template-columns: 5% 30% 20% 45%;
}

.joustick-container {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
}

.zing-container {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
}

.zing-container > img {
    height: 100%;
    width: 100%;
}
.actionBar-item-1 {
    display: flex;
    flex-wrap: wrap;
}

.action-container {
    height: 100%;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}
