.FlowchartDecisionPanel_Container {
    height: 85%;
    width: 84%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 19999;
    overflow: hidden;
    /* border: 3px solid blue; */
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 25% 18% 32% 25%;
    border-radius: 11%;
}
/* first div */
.Flowsub_Container1 {
    display: grid;
    grid-template-columns: 30% 50% 20%;
    grid-template-rows: 100%;
    grid-template-areas:
        'content1_text1 content1_text2 content1_text3'
        ' content1_text1 content1_text2 content1_text3'
        'content1_text1 content1_text2 content1_text3 ';
}
#Flowcontent1_text1 {
    grid-area: content1_text1;
    position: relative;
}
#Flowcontent1_text2 {
    grid-area: content1_text2;
    position: relative;
}
#Flowcontent1_text3 {
    grid-area: content1_text3;
    position: relative;
}

/* second div */
.Flowsub_Container2 {
    border-radius: 10px;
    margin: 0 10% 0 10%;
    background-color: #ebf6fa;
    display: grid;
    grid-template-columns: 20% 20% 20% 20%;
    grid-template-rows: 80% 20%;
    grid-template-areas:
        'content_div1 content_div2 content_div3 content_div4 content_div5'
        'sensors-txt1 sensors-txt2 sensors-txt3 sensors-txt4 sensors-txt5';
}
#Flowcontent_div1 {
    grid-area: content_div1;
    /* background-color: rgb(234, 109, 109); */
    position: relative;
}

