.loader {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: row;
    grid-gap:30px;
    gap:30px;
    align-items: center;
    justify-content: center;
  }
  
  .jimu-primary-loading:before,
  .jimu-primary-loading:after {
    position: absolute;
    top: 0;
    content: '';
  }
  
  .jimu-primary-loading:before {
    left: -19.992px;
  }
  
  .jimu-primary-loading:after {
    left: 19.992px;
    animation-delay: 0.32s !important;
  }
  
  .jimu-primary-loading:before,
  .jimu-primary-loading:after,
  .jimu-primary-loading {
    background: #fa9158;
    animation: loading-keys-app-loading 0.8s infinite ease-in-out;
    width: 13.6px;
    height: 32px;
  }
  
  .jimu-primary-loading {
    text-indent: -9999em;
    position: relative; /* Changed from absolute */
    animation-delay: 0.16s !important;
  }
  
  @keyframes loading-keys-app-loading {
    0%,
    80%,
    100% {
      opacity: .75;
      box-shadow: 0 0 #fa9158;
      height: 32px;
    }
  
    40% {
      opacity: 1;
      box-shadow: 0 -8px #fa9158;
      height: 40px;
    }
  }
  
  .loading-text {
    display: absolute; 
    margin-right:10px;
    font-size: 24px;
    font-weight: 400;
    color: black; 
  }
  
