/* true */
.flowchart-switch-button-true {
    display: inline-block;
    background: #e6e6e6;
    border-radius: 30px;
    overflow: hidden;
    text-align: center;
    color: #717171 !important;
    position: relative;
    right: 8%;
    bottom: -20px;
    opacity: 1;
    width: 210px;
    height: 35px;
    text-align: center;
    position: relative;
    position: relative;
    margin: 10px 0px 0px 0px;
}

.flowchart-switch-button-true input {
    position: absolute;
    opacity: 0;
}
.flowchart-switch-button-true input + label {
    float: left;
    cursor: pointer;
    margin: 0;
    position: relative;
    padding: 5px 0px;
    text-align: end;
    width: 65px;
    display: inline-block;
}
/* .flowchart-switch-button-true input:checked + label {
     background: #30a8ce;
     transition: smooth 300ms;
    } */
#digital-slider-A1,
#digital-slider-A2,
#digital-slider-B1,
#digital-slider-B2,
#digital-slider-C1,
#digital-slider-C2,
#digital-slider-D1,
#digital-slider-D2 {
    background: #30a8ce;
    border-radius: 30px;
    height: 100%;
    width: 35%;
    position: absolute;
    left: 0;
    top: 0;
    transform: translateX(0px);
    transition: transform 200ms linear;
}
.slide-A1-digital,
.slide-A2-digital,
.slide-B1-digital,
.slide-B2-digital,
.slide-C1-digital,
.slide-C2-digital,
.slide-D1-digital,
.slide-D2-digital {
    transform: translateX(0px) !important;
}
.slide-A1-analog,
.slide-A2-analog,
.slide-B1-analog,
.slide-B2-analog,
.slide-C1-analog,
.slide-C2-analog,
.slide-D1-analog,
.slide-D2-analog {
    transform: translateX(75px) !important;
}
.slide-A1-servo,
.slide-A2-servo,
.slide-B1-servo,
.slide-B2-servo,
.slide-C1-servo,
.slide-C2-servo,
.slide-D1-servo,
.slide-D2-servo,
.slide-M3-mp3,
.slide-M4-serialServo {
    transform: translateX(145px) !important;
}

