/* false */
.flowchart-switch-button-false {
    opacity: 0.3;
    display: inline-block;
    background: #e6e6e6;
    border-radius: 30px;
    overflow: hidden;
    text-align: center;
    color: #717171 !important;
    position: relative;
    right: 8%;
    bottom: -20px;
    width: max-content;
    height: 35px;
    text-align: center;
    position: relative;
    position: relative;
    margin: 10px 0px 0px 0px;
    cursor: not-allowed;
}
.flowchart-switch-button-false input {
    position: absolute;
    opacity: 0;
    cursor: not-allowed;
}
.flowchart-switch-button-false input + label {
    cursor: pointer;
    margin: 0;
    position: relative;
    padding: 5px 0px;
    text-align: initial;
    width: 75px;
    display: inline-block;
    cursor: not-allowed;
}

