.outertabDiv-output {
    position: relative;
    top: 42%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #000;
    height: 80vh;
    width: 64vw;
    background-size: 100% 90%;
    background-repeat: no-repeat;
    background-image: url('../../../../../../Assets/img/propertypanel@2x.png');
}
.outertabDiv-outputText {
    height: 50vh;
    width: 156%;
    position: relative;
    color: black;
}

.outertabDiv-outputText > p {
    position: absolute;
    top: 75%;
    left: 32%;
    transform: translate(-50%, -50%);
    text-align: center;
}
