.slider-item1-flowchart {
    /* height: 10vh; */
    width: 36vw;
    display: grid;
    grid-template-columns: 23% 74%;
    grid-template-rows: 100%;
    grid-gap: 8%;
    padding-bottom: 5%;
}

.slider-item1-flowchart .portDetails-flowchart {
    height: 100px;
    width: 120%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #000;
}

.slider-item1-flowchart .portSlider-flowchart {
    position: relative;
    display: flex;
    align-items: center;
    color: #000;
    justify-content: flex-end;
    background: #dff5f2;
    /* border-radius: 10px; */
    -webkit-user-select: none;
    user-select: none;
    padding-left: 1em;
    /* padding-top: 1em; */
    /* top: -22%; */
    /* padding-bottom: 23%;
   */
