}

.checkBox-conatiner {
    /* height: 70%;
  width: 75%; */
    border-radius: 6px;
    position: relative;
    -webkit-user-select: none;
    user-select: none;
}

.renderClickfalse {
    background-color: #e6e6e6;
}
.renderClicktrue {
    background-color: #fff;
}

.isActivePortInfofalse {
    opacity: 0.3;
}
.isActivePortInfotrue {
    opacity: 1;
}

.action-contaniner {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    /* justify-content: space-between; */
}

.disabledBtn,
.enabledBtn {
    /* display: inline-flex;
	justify-content: center;
	align-items: center; */
    width: 30%;
    padding: 1rem;
    height: 50px;
    border: 0;
    border-radius: 1rem;
    font-family: 'Halcyon_SemiBold' !important;
}

.disabledBtn {
    background: linear-gradient(to right, #9f9f9f, #e3e3e3);
}

.enabledBtn {
    background: linear-gradient(to right, #feba45, #f97e61);
    color: #ffffff;
}

