/* Tooltip styles */
.Body_Text2 .tooltiptext {
    position: absolute;
    top: 140%;
    left: 63%;
    visibility: hidden;
    background-color: #3b4441;
    color: #e4e5e4;
    text-align: center;
    padding: 6px 7px;
    font-weight: lighter;
    border-radius: 7px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.Body_Text2:hover .tooltiptext {
    visibility: visible;
}

.tooltiptext > p {
    margin-bottom: -2.5% !important;
    color: #a4a9ac;
    font-size: 1.5vh;
}
