body {
    background-color: #fafafa;
    /* background-color: #e9ecef; */
}
.Speech-Main {
    height: 100vh;
    width: 100vw;

    display: grid;
    /* background-color: #e9ecef; */

    /* border: 1px solid red; */
    grid-template-columns: 100%;
    grid-template-rows: 10% 80% 10%;
}
.Speech_Header {
    height: 100%;
    width: 100%;
    display: grid;
    /* border: 1px solid red; */
    grid-template-columns: 7% 13% 65% 5% 4% 6%;
    grid-template-rows: 100%;
}
.Back_BTNN {
    height: auto;
    width: 4%;
    position: absolute;
    top: 4%;
    left: 2%;
    cursor: pointer;
}
.Play_Speech {
    height: auto;
    width: 4%;
    font-size: 25px;
    font-family: 'Halcyon_SemiBold' !important;
    position: absolute;
    top: 5.5%;
    left: 7%;
}
.Lang_Bar {
    height: 100%;
    width: 16%;
    right: 0;
    display: grid;
    grid-template-rows: 90% 10%;
    grid-template-columns: 100%;
    position: absolute;
    /* z-index: -1; */
}
.Lang_Div {
    height: 20%;
    width: 61%;
    display: grid;
    position: absolute;
    top: 15%;
    left: 17%;
    /* position: relative; */
    /* grid-template-rows: repeat(3, 33%); */
    grid-template-rows: 32% 2% 32% 2% 32%;
    grid-template-columns: 100%;
}

.Language_Bar {
    height: 100%;
    width: 100%;
    right: 0;
    position: absolute;
    z-index: -1;
}
.English_Lang {
    height: 10%;
    width: 10%;
    font-size: 16px;
    /* border-bottom: 3px solid #707070; */
    font-family: 'Halcyon_Regular' !important;
    position: absolute;
    top: 11%;
}
.Hindi_Lang {
    height: 10%;
    width: 10%;
    font-size: 16px;
    top: 45%;
    font-family: 'Halcyon_Regular' !important;
    position: absolute;
}
.LanguageIA_Btn {
    height: auto;
    width: 4%;
    cursor: pointer;
    position: absolute;
    top: 4%;
    right: 10%;
    z-index: 1;
}
.Help_btn {
    height: auto;
    width: 4%;
    position: absolute;
    cursor: pointer;
    top: 4%;
    right: 6%;
    z-index: 1;
}
.Bluetooth_Btn {
    height: auto;
    width: 6%;
    position: absolute;
    top: 4%;
    right: 0.1%;
}
.mole {
    width: 100vw;
    height: 100vh;
    left: 0;
    position: absolute;
}
.Spch_Slider {
    width: 100vw;
    height: 100vh;
    position: absolute;
    left: 0%;
    z-index: 100000;
    background: #fff;
}
.ros {
    position: absolute;
    right: 13%;
    top: 8%;
    height: 10%;
    width: 5%;
    z-index: 100000;
}
.Ace_Speech {
    height: 50%;
    width: 50%;
    display: grid;
    position: absolute;
    top: 50%;
    left: 50%;
    grid-template-columns: repeat(2, 50%);
    grid-template-rows: 100%;
    transform: translate(-50%, -50%);
}
.Ace_img {
    height: auto;
    width: 68%;
    margin-top: 14%;
    margin-left: 4%;
}
.Mic_img {
    height: auto;
    width: 48%;
    cursor: pointer;
    margin-top: 26%;
    margin-left: 53%;
    animation: ripple 1.5s linear infinite;
    border-radius: 100%;
}
.Mic_imgIA {
    height: auto;
    width: 48%;
    cursor: pointer;
    margin-top: 26%;
    margin-left: 53%;

    border-radius: 100%;
}
.Speech_Instruc {
    height: auto;
    width: 33%;
    font-size: 18px;
    font-family: 'Halcyon_Medium' !important;
    color: #707070;
    position: absolute;
    bottom: 6%;
    left: 34%;
}
.Speech_Instruct {
    height: auto;
    width: 33%;
    font-size: 19px;
    font-family: 'Halcyon_Medium' !important;
    color: #707070;
    /* margin-top: 5%; */
    /* margin-left: 30%; */
    position: absolute;
    bottom: 6%;
    left: 44%;
}
.Sound_Wave {
    min-width: 80%;
    min-height: 50%;
    position: fixed;
    z-index: -3;
    top: -31%;
    left: 29.5%;
    /* background-color: #707070; */
}

