.action-card {
    padding-top: 5%;
    margin-left: 10%;
    height: 110%;
    width: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
}

.disabledBtn,
.enabledBtn,
.addBtn {
    width: 90%;
    height: 4rem;

    margin: 1rem;
    border: 0;
    border-radius: 1rem;
    font-family: 'Halcyon_SemiBold' !important;
    z-index: 100;
    /* text-align: left; */
}

.disabledBtn {
    background: linear-gradient(to right, #9f9f9f, #e3e3e3);
    color: black;
}

.enabledBtn {
    background: linear-gradient(to right, #feba45, #f97e61);
    color: #ffffff;
}

.addBtn {
    /* position: absolute; */
    /* width: 16rem; */
    border: 2px dotted black;
    /* top: 30rem; */
    background: none;
}

.action-sidebar-scrollZone {
    height: 80%;
    overflow-x: hidden;
    overflow-y: scroll;
}

.action-sidebar {
    height: 72vh;
    width: 100%;
    background: #ebf6fa;
