.slider {
    -webkit-appearance: none;
    position: absolute;
    left: 32%;
    top: 25%;
    height: 10px;
    width: 60%;
    background: #ffcb6f;
    border-radius: 12px;
}

/* for removing silder button */

.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    height: 30px;
    width: 30px;
    border-radius: 50%;

    background-image: url('../../../Assets/images/Create\ Actions/sliderButton.png');
    background-repeat: no-repeat;
    background-size: 130%;

    background-position: 50% -10%;
}

.slider::after {
    content: '1000';
    color: black;
    font-size: 1.5rem;
    position: absolute;
    left: 80%;
    top: 450%;
}

.slider::before {
    content: '0';
    color: black;
    font-size: 1.5rem;
    position: absolute;
    left: 0%;
    top: 450%;
}

