.value {
    height: 38px;
    width: 35px;
    border: 1px solid #fff;
    border-radius: 256px;
    position: absolute;
    left: 7%;
    top: 50%;
    background-color: white;
    font-family: halcyon-SemiBold !important;

    /* border: 1px solid red; */
}

.value > p {
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: black;
}

.create-backbutton {
    height: 60px;
    width: 60px;
    position: absolute;
    bottom: 2%;
    left: 2%;
    transform: translate((-2%, -5%));
    cursor: pointer;
}

.create-addbutton {
    height: 82px;
    width: 82px;
    position: absolute;
    bottom: 12%;
    left: 5%;
    /* transform: translate((-5%, -2%)); */
    cursor: pointer;
}

.create-nextbutton {
    position: absolute;
    bottom: 2%;
    right: 3%;
    width: 60px;
    /* transform: scale(0.8); */
    cursor: pointer;
}
.create-upload {
    position: absolute;
    bottom: 3.5% !important;
    right: 15%;
    width: 53px;
    /* transform: scale(0.8); */
    cursor: pointer;
    rotate: 180deg;
}

.create-playgroup {
    position: absolute;
    bottom: 2%;
    right: 9%;
    width: 60px;
    cursor: pointer;
}

.create-servoAngle {
    height: 27%;
    width: 10vw;
    /* border: 1px solid red; */
    border-radius: 26px;
    position: absolute;
    top: 50%;
    left: 13%;
    transform: translate(-50%, -50%);
    cursor: pointer;
    font-family: halcyon-SemiBold !important;
    font-size: 22px;
    /* border: 1px solid blue; */
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.create-setTime {
    height: 15%;
    width: 20%;
    background-color: aqua;
}
.tooltip {
    /* position: absolute; */
    /* background-color: #f0f7fa; */
    background-color: #ffffff;
    box-shadow: inset 0 0 0.5rem #e0ebf7;
    border: 1px solid #b6b6db;
    text-align: center;
    padding: 6px 7px;
    font-weight: lighter;
    border-radius: 7px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    /* visibility: hidden; */
    /* top: 15vh; */
    /* left: 94%; */
    transform: translate(-50%, -50%);
}

.savbtn,
.readbtn,
.groupPlaydbtn {
    visibility: hidden;
}
.create-nextbutton:hover + .savbtn,
.create-nextbutton:hover ~ .savbtn {
    visibility: visible;
}
.create-upload:hover + .readbtn,
.create-upload:hover ~ .readbtn {
    visibility: visible;
}
.create-playgroup:hover + .groupPlaydbtn,
.create-playgroup:hover ~ .groupPlaydbtn {
    visibility: visible;
}
.create-playgroup:hover + .groupPlaydbtn,
.create-playgroup:hover ~ .groupPlaydbtn {
    visibility: visible;
}

@media (min-width: 768px) and (max-width: 1224px) {
    .create-Select {
        width: 300px;
    }
    .create-helpbutton {
        height: 73%;
        width: 40px;
        margin-right: 40px;
    }

    .create-bluetooth {
        height: 50px;
        width: 80px;
        margin-top: 5px;
    }
    .create-savebutton {
        height: 73%;
        width: 40px;
        margin-right: 60px;
    }
}

@media (max-width: 768px) {
    .create-Select {
        width: 200px;
    }
    .create-helpbutton {
        height: 73%;
        width: 40px;
        margin-right: 40px;
    }

    .create-bluetooth {
        height: 50px;
        width: 80px;
        margin-top: 5px;
    }
    .create-savebutton {
        height: 73%;
        width: 40px;
        margin-right: 60px;
    }
}

@media (max-width: 900px) {
    .sc-toggle-button {
        height: 22px;
        width: 22px;
    }
    .create-backbutton,
    .create-nextbutton {
        height: 62px;
        width: 62px;
    }
}

@media (max-width: 768px) {
    .create-popupcard {
        height: 35%;
        width: 90%;
    }

    .create-servoAngle {
        height: 50%;
        width: 55px;
        font-size: 16px;
    }

    .create-detail-container {
        font-size: 12px;
    }

    .value {
        height: 20px;
        width: 30px;
    }

    .slider::after {
        font-size: 12px;
    }

    .slider::before {
        font-size: 12px;
    }
}

@media (min-width: 768px) and (max-width: 1224px) {
    .slider::after {
        font-size: 20px;
    }

    .slider::before {
        font-size: 20px;
    }

    .value {
        height: 25px;
        width: 50px;
        position: absolute;
        left: 0%;
        top: 50%;
    }
    .create-servoAngle {
        height: 50%;
        width: 55px;
        font-size: 16px;
    }
}
