#SaveAlert {
    visibility: hidden;
    min-width: 160px;
    margin-left: -75px;
    background-color: #e8e6e6;
    color: #000;
    text-align: center;
    border-radius: 25px;
    font-family: 'Halcyon_Regular' !important;
    padding: 12px;
    position: fixed;
    z-index: 1;
    left: 50%;
    bottom: 10%;
    font-size: 17px;
}
#SaveAlert.show {
    visibility: visible;
    -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
    animation: fadein 0.5s, fadeout 0.5s 2.5s;
}
