    /* margin-top: 1%; */
    position: absolute;
    left: -4%;
    z-index: 100000;
    background: #fff;
}
.hpClose {
    position: absolute;
    margin-top: 45%;
    margin-left: 92%;
    /* height: 10%; */
    /* width: 5%; */
    z-index: 100000;
}

@media screen and (max-width: 1024px) and (min-width: 768px) {
    .btn {
        font-size: 1vw;
    }
}

@media screen and (max-width: 768px) and (min-width: 450px) {
    .btn {
        font-size: 0.9vw;
    }
}
