.Main_Container {
    display: grid;
    height: 100%;
    width: 100%;
    grid-template-columns: 15% 5% 80%;
    grid-template-rows: 80% 20%;
    grid-template-areas:
        'sidebar arrowBtn body'
        'sidebar arrowBtn bottom';
}

.Sensor_Body {
    display: flex;
    height: 80%;
    width: 100%;
}
.Sensors_Container {
    /* border: 2px solid red; */
    display: grid;
    grid-template-columns: 33% 33% 33%;
    grid-template-rows: 17% 16.5% 16.5% 16.5% 16.5% 16.5%;
    height: 100%;
    width: 100%;
    position: relative;
    top: -8%;
}
.Sensors_Container > div > img {
    width: 35vh;
}
.time_headers{
    position: absolute;
    top: 28%;
    left: 1%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}
.time_headers p{
    width: 15%;
    text-align: center;
}

.Container {
    position: relative;
}
.sub_Container {
    height: 54%;
    width: 67%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
}
.sub_Container p {
    pointer-events: none;
}
.Container > div {
}
.Container > div > p {
    font-size: 2.5vh;
    position: absolute;
    top: 55%;
    left: 45%;
    transform: translate(-50%, -50%);
    width: 72%;
    word-wrap: break-word;
    hyphens: auto;
}

.Ports_Container {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 33% 33% 33%;
    height: 100%;
    width: 100%;
}

.Ports_Container > div > img {
    width: 40vh;
}

#sidebar {
    grid-area: sidebar;
    z-index: 2;
    overflow-y: scroll;
    /* display: grid;
    grid-template-columns: 100%; */
}
#head {
    border: '2px solid blue';
}

