#bottom {
    grid-area: bottom;
    transform: translateY(104%) translateX(-5%);
}
.sub_Bottom {
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    flex-wrap: nowrap;
    gap: 1%;
    transform: translateX(-1%);
}

.sub_Bottom2 {
    position: absolute;
    right: 0%;
    top: 5vh;
    width: 55px;
    height: 55px;
    transform: translate(-50%, -50%);
    cursor: pointer;
}

