.helpScreen {
    height: 100vh;
    width: 100vw;
    background: rgb(255, 255, 255);
    position: absolute;
    top: 49%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
}
.help {
    position: relative;
    transform: translate(-50%, -50%);
    border-radius: 7px;
    z-index: 1000;
}

.help_text {
    background-color: white;
    padding: 2px;
    font-size: 1.8vh;
    text-align: left;
    color: rgb(112, 112, 112);
    font-family: Halcyon_Regular;
    border-radius: 12px;
    margin-bottom: 0px !important;
}
